<template>
 <div class="center">
    <div class="center_breadcrumb">
       <crumb v-if="isOperate ==='taskSituation'" :data="pageTitle"></crumb>
       <crumb v-else></crumb>
    </div>
    <div class="center_content">
        <!-- <div class="center_content_title">
             <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div> -->
        <div class="center_content_search" style="padding-bottom:0">
            <div class="center_content_search_left" style="margin-bottom: 10px;width:98%">
                <div class="search-input-suffix gap down-gap">
                    <label>课程性质：</label>
                    <el-select v-model="page.courseProperty" placeholder="请选择">
                        <el-option
                        v-for="item in fieldCoursePropertyOptions"
                        :key="item.code"
                        :label="item.value"
                        :value="item.code">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix gap down-gap">
                    <label>课程类型：</label>
                    <el-select v-model="page.classificationId" filterable placeholder="请选择">
                        <el-option
                        v-for="item in courseClassification"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix gap down-gap">
                    <label>建设情况：</label>
                    <el-select v-model="page.constructionSituation" filterable placeholder="请选择">
                        <el-option
                        v-for="item in constructionOption"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix gap down-gap" style="float: none;">
                    <label>课程搜索：</label>
                    <el-input class="search"
                    style="width:217px"
                            @keyup.enter.native="search"
                            v-model="page.keywords"
                            placeholder="请输入课程名称"
                            >
                    </el-input>
                </div>
              <div class="search--btn"  @click="search" style="margin: 0 0 0 20px">搜索</div>
            </div>
            <div class="center_content_search_right">
                <!-- <el-button @click="openAdd" type="primary" icon="el-icon-plus" round>新建</el-button> -->
            </div>
        </div>
         <div class="table-container" style="padding-bottom: 4px;margin-bottom: 115px;">
             <el-table
                :data="tableData"
                stripe
                style="width: 130">
                <el-table-column
                prop="code"
                label="课程编码"
                >
                </el-table-column>
                <el-table-column
                prop="name"
                label="课程名称">
                </el-table-column>
                <el-table-column
                prop="classificationVal"
                label="课程类型">
                </el-table-column>
                <el-table-column
                prop="credit"
                label="总学分">
                </el-table-column>
                <el-table-column
                prop="period"
                label="总学时">
                </el-table-column>
                <el-table-column
                prop="termVal"
                label="开课学期">
                </el-table-column>
                <el-table-column
                prop="leaderName"
                label="课程负责人">
                </el-table-column>
                <!-- <el-table-column
                prop="examineList"
                label="考核方式">
                <template slot-scope="scope">
                    <span v-for="(vo,index) in scope.row.examineList" :key="index" >{{index === scope.row.examineList.length - 1 ? vo.examineVal : vo.examineVal + '、'}}</span>                   
                </template>
                </el-table-column> -->
                <el-table-column
                prop="constructionSituation"
                label="课程建设情况">
                </el-table-column>
                <el-table-column
                v-if="isOperate !=='taskSituation'"
                label="操作"
                width="260">
                <template slot-scope="scope">
                    <!-- <div class="operate-button distance">
                         <SvgIcon icon-class="icon-edit-line" size="15px"></SvgIcon>
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button distance">
                        <SvgIcon icon-class="icon_shanchu" size="15px"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small">
                        删除
                        </el-button>
                    </div>  -->
                    <div class="operate-button distance" style="margin-right:6px">
                         <SvgIcon icon-class="iconkechengshezhi"></SvgIcon>
                        <el-button
                        @click.native.prevent="setRowConstruction(scope.row)"
                        type="text"
                        size="small">
                        课程建设
                        </el-button>
                    </div>  
                     <div class="operate-button" style="margin-right:6px">
                         <SvgIcon icon-class="iconyulan"></SvgIcon>
                        <el-button
                        @click.native.prevent="rowPreview(scope.row)"
                        type="text"
                        size="small">
                        预览大纲
                        </el-button>
                    </div>  
                    <div class="operate-button" style="margin-right:6px">
                         <SvgIcon icon-class="iconkechengshezhi"></SvgIcon>
                        <el-button
                        @click.native.prevent="setRow(scope.row)"
                        type="text"
                        size="small">
                        课程设置
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination" style="margin-bottom: 42px;">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totals">
                </el-pagination>
            </div>
        </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="课程名称" prop="name">
                <el-input v-model="form.name"  placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="培养方案版本" prop="versionId">
                <el-select v-model="form.versionId" placeholder="请选择培养方案版本">
                    <el-option
                        v-for="item in versionOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="课程编码" prop="code">
              <el-input v-model="form.code"  placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="课程分类" prop="classificationId">
            <el-select v-model="form.classificationId" filterable placeholder="请选择课程分类">
                <el-option
                    v-for="item in courseClassifications"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="课程类型" prop="courseProperty">
            <el-select v-model="form.courseProperty" placeholder="请选择课程类型">
                <el-option
                    v-for="item in fieldCoursePropertyOptionsed"
                    :key="item.code"
                    :label="item.value"
                    :value="item.code">
                </el-option>
            </el-select>
            </el-form-item>
             <el-form-item label="学分" prop="credit">
                <el-input-number v-model="form.credit" controls-position="right" :min="0.1" :max="9999"></el-input-number>
            </el-form-item>
             <el-form-item label="总学时" prop="period">
                 <el-input-number v-model="form.period" controls-position="right" :min="1" :max="9999"></el-input-number>
            </el-form-item>
             <el-form-item label="开课学期" prop="term">
                 <el-select v-model="form.term" filterable placeholder="请选择开课学期">
                    <el-option
                        v-for="item in fieldTermOptions"
                        :key="item.code"
                        :label="item.value"
                        :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button  @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
    <!-- 设置课程 -->
    <set-course v-model="setCourseDialogFormVisible" type='hubei' :course-data="setCourseData" @close="closeSetCourseDialog" @success="successSetCourseDialog"/>
 </div>
</template>

<script>
import trainingProgramApi from '@/api/trainingProgramApi';
import SetCourse from '../components/SetCourse.vue'
export default {
 name: 'CourseManagement',
 components:{SetCourse},
 data() {
 return {
    pageTitle:['课程建设', '课程大纲建设'],
    isOperate:this.$route.query.type, //教学任务管理-任务完成情况跳转过来携带 type:taskSituation无操作权限
    totals:0,
    isAdd:true,
    tableData: [],
    userDialogFormVisible:false,
    setCourseDialogFormVisible:false,
    setCourseData:{},
    versionOptions:[],
    courseClassification:[],
    fieldCoursePropertyOptions:[],
    courseClassifications:[],
    fieldCoursePropertyOptionsed:[],
    fieldTermOptions:[],
    constructionOption:[
        {
            name:'全部',
            id:''
        },
        {
            name:'已建设',
            id:'已建设'
        },
        {
            name:'未建设',
            id:'未建设'
        }
    ],
    form: {
        versionId:'',
        code:'',
        classificationId:'',
        name:'',
        courseProperty:'',
        credit:'',
        period:'',
        term:null,
        id:undefined
        },
    page:{
        versionId:localStorage.getItem('versionId'),
        classificationId:'',
        courseProperty:null,
        constructionSituation:'',
        keywords:'',
        term:'',
        page:1,
        rows:15
     },
     rules:{
        name: [
            {
              required: true,
              message: '请输入课程名称',
              trigger: 'blur'
            }
          ],
          versionId: [
            {
              required: true,
              message: '请选择培养方案版本',
              trigger: 'blur'
            }
          ],
           code: [
            {
              required: true,
              message: '请输入课程编码',
              trigger: 'blur'
            }
          ],
           classificationId: [
            {
              required: true,
              message: '请选择课程分类',
              trigger: 'blur'
            }
          ],
           courseProperty: [
            {
              required: true,
              message: '请选择课程类型',
              trigger: 'blur'
            }
          ],
           credit: [
            {
              required: true,
              message: '请输入学分',
              trigger: 'blur'
            }
          ],
           period: [
            {
              required: true,
              message: '请输入总学时',
              trigger: 'blur'
            }
          ],
           term: [
            {
              required: true,
              message: '请输入开课学时',
              trigger: 'blur'
            }
          ]
    }
 }
 },
 created(){},
 mounted(){
    this.initFn()
 },
 computed:{
     dialogTitle(){
        return this.isAdd ? '添加课程' : '编辑课程'
     }
 },
 methods:{
     initFn(){
        this.initData()
        this.versionAll()
        this.fieldCourseProperty()
        this.courseClassificationAll()
        this.fieldTerm()
       
     },
     initData(){
        //  const url =trainingProgramApi.courseList;
         this.$post('basic/course/list',this.page).then(res =>{
             if(res.status === 1){
                 this.totals=+ res.data.total;
                 this.tableData=res.data.list;                                  
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     },
     //培养方案版本
     versionAll(){
         const url =trainingProgramApi.versionAll;
         this.$get(url).then(res =>{
             if(res.status === 1){               
                this.versionOptions=res.data;
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     },
     //课程分类
     courseClassificationAll(){
         const url =trainingProgramApi.courseClassificationAll;
         this.$get(url).then(res =>{
             if(res.status === 1){
                const allArr={
                    id: '',
                    name:'全部'
                 }                         
                 this.courseClassifications=JSON.parse(JSON.stringify(res.data))
                 this.courseClassification=res.data  
                 this.courseClassification.unshift(allArr)   
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     }, 
     //课程性质（课程类型）
     fieldCourseProperty(){
         const url =trainingProgramApi.fieldCourseProperty;
         this.$get(url).then(res =>{
             if(res.status === 1){
                const allArr={
                    code: null,
                    value:'全部'
                 }    
 
                 this.fieldCoursePropertyOptionsed=JSON.parse(JSON.stringify(res.data))       
                 this.fieldCoursePropertyOptions=res.data   
                this.fieldCoursePropertyOptions.unshift(allArr)        
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     },
     //开课学期
      fieldTerm(){
         const url =trainingProgramApi.fieldTerm;
         this.$get(url).then(res =>{
             if(res.status === 1){
                  this.fieldTermOptions=res.data

             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         })
     }, 
     setRowConstruction(row){
         this.$router.push({
             path:'/Construction/basicInfo',
             query:{
                id:row.id
             }
         })
     },
     rowPreview(row){
          this.$router.push({
             path:'/Construction/previewCourse',
             query:{
                id:row.id
             }
         })
     },
     headInfo(item){
        this.pageTitle=item[1]
     },
    search(){
        this.resetTemp();
        this.initData() ;
     },
     handleSizeChange(val){
        this.page.rows = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
     openAdd(){         
          this.userDialogFormVisible = true;
          this.form={
                versionId:'',
                code:'',
                classificationId:'',
                name:'',
                courseProperty:'',
                credit:'',
                period:'',
                term:null,
                id:undefined
        };  
        this.isAdd=true;
        this.$nextTick(() =>{
            this.$refs['form'].resetFields();
        })
     },
    deleteRow(row){
        this.$confirm('确定删除该课程?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
           this.userDel(row.id)
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });        
     },
     editRow(row){
        const obj={
                versionId:row.versionId,
                code:row.code,
                classificationId:row.classificationId,
                name:row.name,
                courseProperty:row.courseProperty,
                credit:row.credit,
                period:row.period,
                term:row.term,
                id:row.id
        }
        this.form=obj;
        this.userDialogFormVisible = true; 
        this.isAdd=false;
     },
     setRow(row){
       this.setCourseData=row;
       this.setCourseDialogFormVisible=true;

     },
     closeSetCourseDialog(val){
       this.setCourseDialogFormVisible=val;
     },
     successSetCourseDialog(val){
        this.courseSet(val);
        this.initData() 
     },
     create(formName){            
       this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.isAdd){
                this.userAdd()
            }else{   
                this.userEdit()             
                
            }           
                     
          } 
        });
     },
    setCourseCreate(formName){            
       this.$refs[formName].validate((valid) => {
          if (valid) {                    
                     
          } 
        });
     },
     cancel(formName){
        this.userDialogFormVisible = false;
        this.$refs[formName].resetFields();
     },
     handleDialogClose(){
        this.$refs['form'].resetFields();
        this.userDialogFormVisible = false;    
     },
    setCourseCancel(formName){
        this.setCourseDialogFormVisible = false;
        this.$refs[formName].resetFields();
     },
     userAdd(){
        const url =trainingProgramApi.courseAdd;
       this.$post(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.resetTemp()
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                 console.log(err)
            })
     },
     userDel(id){
       const url =trainingProgramApi.courseDel({
           id:id
       });
        this.$deletes(url)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        
                        })
                    this.initData() ;
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                })
                }
                
        }).catch(err =>{
             console.log(err)
        })
     },
     userEdit(){
        const url =trainingProgramApi.courseEdit;
        this.$put(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                console.log(err)
            })
     },
     courseSet(formData){
        const url =trainingProgramApi.courseSet;
        this.$put(url,formData)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })                       
                        this.initData();
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                console.log(err)
            })
     },
     resetTemp(){
        this.page.page=1;
        this.page.rows=15;
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
    .el-icon-search:before {
        font-size: 16px;
    }
</style>



